.container {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    background-color: #2f303a;
    box-shadow: 0px 0px 8px rgba(0,0,0,.6);
    color: #fff;
};

.contacts_container {
    padding: 20px;
}

.address {
    margin-top: 8px;
    font-style: normal;
}

.contacts_item {
    margin-top: 12px;
    margin-bottom: 0px;
};

.contacts_link {
    text-decoration: none;
    color: #fff;

    &:hover {
        color: orange;
    }
};
